/** @format */

.ql-editor {
  width: 8.5in;
  min-height: 10in;
  padding: 1in;
  margin: 1rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  background: white;
}

.ql-container.ql-snow {
  border: none;
  display: flex;
  justify-content: center;
}

.ql-toolbar.ql-snow {
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: #f3f3f3;
  border: none;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
}

@page {
  margin: 1in;
}

@media print {
  body {
    background: none;
  }

  .ql-editor {
    width: 6.5in;
    height: 8in;
    padding: 0px;
    margin: 0px;
    box-shadow: none;
    align-self: flex-start;
  }
  .ql-toolbar.ql-snow {
    display: none;
  }
}
